
import FieldSelf from './FieldSelf'
import FieldOpponent from './FieldOpponent'
import Opponent from './Opponent'
import Self from './Self'
import Hand from './Hand'
import React, {Component} from 'react'
import axios from 'axios'
import { isConstructorDeclaration } from 'typescript'




class Board extends React.Component { 
//  function Board({parentToChild}){
  constructor(props) {
    super(props);
    this.state = { name:'', p1name: '', p2name: '', winner1:'', winner2: '',
      game:{
        b: '',
        currentTurn: '',
        uuid: '',
      },    
      p1:{
        id: '',
        name: '',
        deck:[],
        hand:[],
        mana:'',
        life:'',
        board:[],
      },
      p2:{
        id: '',
        name: '',
        deck:[],
        hand:[],
        mana:'',
        life:'',
        board:[],
      }         
    };
    this.getGame = this.getGame.bind(this);
  }

    componentDidMount() {
        this.setName();
        this.timer = setInterval(()=> { this.getGame(); this.checkWinner();/*console.log(this.state.p1.deck[0].attack);*/ console.log(this.state.name);
          this.checkName(); }, 1000);
        console.log(this.props.parentToChild); 
        
      }
    
    //   componentWillUnmount() {
    //     this.timer = null;
    //   }
    checkName(){
     // if(this.state.game.data.p1.name == this.state.name){ console.log("Success")}
     if(this.state.p1.name == this.state.name){
      console.log("Names Equal!!");
     }
    }

      setName() {
        axios.get('http://localhost:8080/home/principal').then( response => { this.setState({name: response.data.name}); 
        } );
        
      }
      
    
      getGame() {
        
        // if(this.props.parentToChild != ''){
        //   let url = `http://localhost:8080/game/games/${this.props.parentToChild}`;
        //   axios.get(`${url}`).then(function (response) { console.log(response); this.setState({game: response}); } );
        // }
        if(this.props.parentToChild != ''){
          let url = `http://localhost:8080/game/games/${this.props.parentToChild}`;
          axios.get(`${url}`).then( response => { console.log(response); this.setState({game: response.data}); this.setState({p1: response.data.p1}); 
          this.setState({p2: response.data.p2}); } );
        }
        
        console.log(this.state.game);
      }

     

      checkWinner(){
        if(this.state.p1 != null && this.state.p2 != null){
          if(this.state.p1.life != null && this.state.p2.life != null){
            if(this.state.p2.life < 0){
                this.setState({winner1: 'You Win!'});
                this.setState({winner2: 'You Lose'});
                setTimeout(() => { axios.get(`http://localhost:8080/${this.state.game.uuid}/customlogout`);  }, 5000);
            }
            else if(this.state.p1.life < 0){
              this.setState({winner1: 'You Lose'});
              this.setState({winner2: 'You Win!'})
              setTimeout(() => { axios.get(`http://localhost:8080/${this.state.game.uuid}/customlogout`);  }, 5000);
            }
          }
        }
      }

    render(props) {

    
      if(this.state.p1.name == this.state.name && this.state.p2 != null){
          return (<header className="App-header">
        <div style={{textAlign:'center',fontSize: 30}}> {this.state.winner1} </div>
        <div className='container'>
          <Opponent life={this.state.p2.life} mana={this.state.p2.mana} />
          <FieldOpponent cards={this.state.p2.board} uuid={this.state.game.uuid} />
          <FieldSelf cards={this.state.p1.board} uuid={this.state.game.uuid}/> 
          <Self life={this.state.p1.life} mana={this.state.p1.mana}/>
        </div>
        <div className='container'>
          <Hand cards={this.state.p1.hand} uuid={this.state.game.uuid}/>
        </div>
      </header>);
      }
      else if( this.state.p2 != null && this.state.p2.name == this.state.name){
        return (<header className="App-header">
          <div style={{textAlign:'center',fontSize: 30}}> {this.state.winner2} </div>
          <div className='container'>
            <Opponent life={this.state.p1.life} mana={this.state.p1.mana}/>
            <FieldOpponent cards={this.state.p1.board} uuid={this.state.game.uuid}/>
            <FieldSelf cards={this.state.p2.board} uuid={this.state.game.uuid} />
            <Self life={this.state.p2.life} mana={this.state.p2.mana}/>
          </div>
          <div className='container'>
            <Hand cards={this.state.p2.hand} uuid={this.state.game.uuid} />
          </div>
        </header>);  
        
      }
      return (<header className="App-header">
          <div className='container'>
            <Opponent />
            <FieldOpponent/>
            <FieldSelf/>
            <Self/>
          </div>
          <div className='container'>
            <Hand />
          </div>
        </header>);  
      
      
    }
    
  }
  export default Board